





















































































































































































import { Component, Vue } from 'vue-property-decorator'
import TableList from '@/components/TableList/index.vue'
import Pagination from '@/components/Pagination/index.vue'
import Edit from './edit.vue'
import { fetchList, deleteData, uploadFile } from '@/api/sysManage/sysExcel'
import { ListQuery } from '@/domains/listQuery'
import { Column } from '@/domains/column'
import { Sort } from '@/domains/sort'
import { User } from '@/domains/user'
import { Pages } from '@/domains/pages'
import { checkSelection } from '@/utils/checkSelection'
import sortable from '@/utils/sortable'
import { getToken } from '@/utils/auth'
import { baseUrl } from '@/utils/config'

class Query extends ListQuery {
  public excelConfigTablename: string = ''
  public key: string = ''
}

class Page extends Pages {
  public list: User[] = []
}

@Component({
  name: 'SysExcel',
  components: {
    TableList,
    Pagination,
    Edit
  }
})

export default class SysExcel extends Vue {
  /** 是否显示table列表 */
  private isShowList: boolean = true
  /** 查询条件 */
  private listQuery: Query = new Query()
  /** 数据列表 */
  private list: User[] = []
  /** 数据列表的id集合，用于拖拽列表 */
  private newList: string[] = []
  /** 数据模型 */
  private columns: Column[] = [
    {
      label: '序号',
      key: 'excelConfigId'
    },
    {
      label: '关键词',
      key: 'excelConfigKey'
    },
    {
      label: '表名',
      key: 'excelConfigTablename'
    },
    {
      label: '不导入字段',
      key: 'excelConfigExcludeColumn'
    }
  ]
  /** 数据总数 */
  private total: number = 0
  /** 新增还是编辑 */
  private type: string = ''
  /** 当前编辑数据的ID */
  private id: string = '0'
  /** 当前选中的数据 */
  private multipleSelection: User[] = []
  /** 唯一标识id的key值 */
  private idKey: string = 'excelConfigId'
  private BASE_API: string = ''
  // 是否显示，导出Excel时填写信息
  private isShowDialog: boolean = false
  private formLabelWidth: string = '100px'
  private form: any = {
    sheetName: '',
    titleName: ''
  }
  private token: string | undefined = ''
  private fileData: FormData = new FormData()
  private isShowImportDialog: boolean = false
  private fileList: any[] = []
  private uploadIndex: number = 0
  private uploadType: any[] = []
  created() {
    this.BASE_API = baseUrl
    this.token = getToken()
    this.getList()
  }
  /** 获取数据列表 */
  private getList(): void {
    fetchList(this.listQuery).then((response: any) => {
      const page: Page = response.page
      this.list = page.list
      this.total = page.totalCount
      // this.newList = this.list.map((v: User) => v.userId)
      this.newList = this.list.map((v: User) => v[this.idKey] as string)
      const el = document.querySelectorAll('.el-table__body-wrapper > table > tbody')[0]
      sortable(el, this.list, this.newList)
    })
  }
  /** 搜索 */
  private handleFilter(): void {
    this.listQuery._search = true
    this.listQuery.page = 1
    this.getList()
  }
  /** 新增 */
  private handleCreate(): void {
    this.type = 'save'
    this.isShowList = false
  }
  /** 编辑 */
  private handleEdit(): void {
    checkSelection(this.multipleSelection, 'one').then(() => {
      this.id = this.multipleSelection[0][this.idKey] as string
      this.type = 'edit'
      this.isShowList = false
    })
  }
  /** 删除数据 */
  private handleDelete(): void {
    checkSelection(this.multipleSelection).then(() => {
      this.$confirm('是否确定删除该数据?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        const ids: string[] = []
        this.multipleSelection.map(item => {
          ids.push(item[this.idKey] as string)
        })
        deleteData(ids).then(() => {
          this.$message({
            type: 'success',
            message: '删除成功!'
          })
          this.handleFilter()
        })
      }).catch(() => {})
    })
  }
  /** 排序 */
  private handleSortChange(sort: Sort): void {
    if (sort.sidx !== undefined) {
      this.listQuery.sidx = sort.sidx
    }
    this.listQuery.order = sort.order
    this.handleFilter()
  }
  private handleSelectionChange(val: User[]): void {
    this.multipleSelection = val
  }
  /** 监听子组件，执行方法 */
  private getMsgFromChild(v: string): void {
    if (v !== 'cancel') {
      this.handleFilter()
    }
    this.isShowList = true
  }
  // 导出
  private handleExport(): void {
    checkSelection(this.multipleSelection).then(() => {
      // this.isShowDialog = true
      this.handleExportExcel()
    })
  }
  private handleExportExcel(): void {
    checkSelection(this.multipleSelection).then(() => {
      const keys: string[] = []
      this.multipleSelection.forEach(item => {
        keys.push(item.excelConfigKey as string)
      })
      const url = this.BASE_API + '/excelfile/toExportExcel?key=' + keys.join() + '&token=' + getToken()
      window.open(url)
      // this.isShowDialog = false
    })
  }
  // 下载Excel模版
  private handleDown(): void {
    checkSelection(this.multipleSelection).then(() => {
      const keys: string[] = []
      this.multipleSelection.forEach(item => {
        keys.push(item.excelConfigKey as string)
      })
      const url = this.BASE_API + '/excelfile/toExportExcelModel?key=' + keys.join() + '&token=' + getToken()
      window.open(url)
    })
  }
  // 导入
  private handleImport(): void {
    checkSelection(this.multipleSelection).then(() => {
      this.isShowImportDialog = true
      this.uploadType = []
      this.uploadType.length = this.multipleSelection.length
    })
  }
  private handleChange(file: File): void {
    this.fileData.append('file', file)
  }
  private uploadFile(file: any): void {
    this.fileData.append('file', file.file)
  }
  // 导入成功
  private handleSuccess(response: any): void {
    this.$message({
      message: response.msg,
      type: response.code === 500 ? 'error' : 'success',
      duration: 5 * 1000
    })
  }
  private submitUpload(): void {
    // this.$refs.upload.submit();
    console.log(this.fileList)
    for (let i = 0; i < this.fileList.length; i++) {
      const fileData = new FormData()
      for (let j = 0; j < this.fileList[i].length; j++) {
        fileData.append('file', this.fileList[i][j])
      }
      fileData.append('key', this.multipleSelection[i].excelConfigKey as string)
      uploadFile(fileData).then((response: any) => {
        this.$message({
          message: response.msg,
          type: response.code === 500 ? 'error' : 'success',
          duration: 5 * 1000
        })
        if (response.msg === '保存成功！') {
          this.$set(this.uploadType, i, true)
        }
      })
    }
  }
  private fileTrigger(i: number): void {
    console.log(document.getElementById('file'))
    this.uploadIndex = i
    const file: any = document.getElementById('file')
    file.click()
  }
  private uploadFileChange(): void {
    const file = document.getElementById('file') as HTMLInputElement
    if (file.files !== null) {
      const fileList = []
      for (var item of file.files) {
        fileList.push(item)
      }
      this.$set(this.fileList, this.uploadIndex, fileList)
    }
  }
  private deleteFile(i: number, index: number): void {
    this.fileList[i].splice(index, 1)
  }
  private deleteFileList(i: number) {
    this.fileList.splice(i, 1)
    const row = this.multipleSelection.splice(i, 1)
    this.uploadType.splice(i, 1)
    // console.log(this.$refs.tableList)
    // console.log(this.$refs.tableList.$refs.table.toggleRowSelection)
    const tableList: any = this.$refs.tableList
    tableList.$refs.table.toggleRowSelection(row[0])
  }
}
