<template>
  <div class="app-container">
    <el-form ref="ruleForm" :model="ruleForm" label-width="100px" class="demo-ruleForm">
      <el-form-item label="表名" class="test test-1">
        <el-tree
          ref="tree"
          :data="data"
          :props="defaultProps"
          :default-checked-keys="selectNodeArray"
          class="scrollbar"
          show-checkbox
          default-expand-all
          node-key="id"
          highlight-current
          @check-change="getCurrentKey"
          @node-click="getCheckedNodes"
        />
      </el-form-item>
      <!-- <el-form-item label="表名">
        <el-input v-model="ruleForm.excelConfigTablename" placeholder="表名" />
        <el-select v-model="ruleForm.excelConfigTablename" clearable placeholder="请选择表名" @change="selectTableNameChange">
          <el-option
            v-for="item in tableNameList"
            :key="item"
            :label="item"
            :value="item"
          />
        </el-select>
      </el-form-item> -->
      <el-form-item label="关键词">
        <el-input v-model="ruleForm.excelConfigKey" placeholder="关键词" />
      </el-form-item>
      <el-form-item label="过滤字段名">
        <!-- <el-input v-model="ruleForm.excelConfigExcludeColumn" placeholder="过滤字段名" /> -->
        <el-select v-model="ruleForm.excelConfigExcludeColumn" multiple clearable placeholder="请选择过滤字段名">
          <el-option
            v-for="item in tableColumnList"
            :key="item.name"
            :label="item.comment"
            :value="item.name"
          />
        </el-select>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="submitForm('ruleForm')">
          确定
        </el-button>
        <el-button @click="cancel">
          取消
        </el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import { saveData, fetchEditInfo, editData, fetchTableNameList, fetchTableColumnList } from '@/api/sysManage/sysExcel1'
export default {
  props: {
    id: {
      type: Number,
      default: 0
    },
    type: {
      type: String,
      default: 'save',
      required: true
    }
  },
  data() {
    return {
      ruleForm: {
      },
      tableColumnList: [],
      tableNameList: [],
      data: [],
      defaultProps: {
        children: 'children',
        label: 'label'
      },
      selectNode: [],
      selectNodeArray: [],
      getNodes: [],
      number: 0,
      dataList: [],
      str: '',
      flag: false,
      columnList: [],
      excelConfigExcludeColumn: [],
      watchNode: ''
    }
  },
  watch: {
    'columnList'(val) {
      // 追加值
      const data = this.excelConfigExcludeColumn
      for (const obj in this.columnList) {
        let num = 0
        for (const obj2 in data) {
          if (this.columnList[obj] === data[obj2]) {
            num = 1
            break
          }
        }
        // 不存在就添加到columnList保存新过滤字段
        if (num <= 0) {
          data.push(this.columnList[obj])
        }
      }
      if (this.watchNode !== '') {
        this.getTableColumnLists(this.watchNode, data)
      }
    },
    'ruleForm.excelConfigExcludeColumn'(val) {
      this.columnList = val // sex
      console.log('columnList: ' + this.columnList)
    }
  },
  created() {
    // this.getTableNameList()
  },
  mounted() {
    this.getTableNameList()
  },
  methods: {
    // 取消勾选
    clearNode(val) {
      const list = this.excelConfigExcludeColumn
      for (const obj in this.tableColumnList) {
        for (const obj2 in list) {
          if (this.tableColumnList[obj].name === list[obj2]) {
            list.splice(obj2, 1)
          }
        }
      }
      // 移除取消勾选的表
      this.getSelectNode(val, 0)
      this.tableColumnList = []
      this.ruleForm.excelConfigExcludeColumn = []
      this.watchNode = ''
    },

    // 保存/移除勾选的表
    getSelectNode(val, number) {
      // 判断是否是新增的值，是则清空下拉框值
      let num = 0
      for (const obj in this.selectNodeArray) {
        if (val.id === this.selectNodeArray[obj]) {
          num = 1
        }
      }
      if (num < 1) {
        this.watchNode = val.label
        this.getTableColumnList(val.label)
      }
      console.log('selectNodeArray: ' + this.selectNodeArray)
      num = 0
      let index = 0
      for (const obj in this.selectNode) {
        if (this.selectNode[obj] === val.label) {
          index = obj
          num = 1
        }
      }
      if (number >= 1) {
        if (num <= 0) {
          this.selectNode.push(val.label)
        }
      } else {
        if (num >= 1) {
          this.selectNode.splice(index, 1)
        }
      }
    },

    // 获取选中节点对象，状态
    getCurrentKey(val, checked, indeterminate) {
      if (!checked) {
        this.clearNode(val)
      } else {
        this.getSelectNode(val, 1)
        // this.watchNode = val.label
        if (this.ruleForm.excelConfigExcludeColumn !== null &&
          this.ruleForm.excelConfigExcludeColumn !== [] &&
          this.ruleForm.excelConfigExcludeColumn.length > 0 &&
          this.number === 0) {
          this.excelConfigExcludeColumn = JSON.parse(JSON.stringify(this.ruleForm.excelConfigExcludeColumn))
          this.number = 1
        }
        if (this.type !== 'edit') {
          this.getTableColumnList(val.label)
        }
      }
    },

    // 获取点击节点key
    getCheckedNodes(val) {
      this.watchNode = val.label
      this.ruleForm.excelConfigExcludeColumn = JSON.parse(JSON.stringify(this.excelConfigExcludeColumn))
    },

    // 根据表名获取字段
    getTableColumnList(val) {
      const data = {
        tableName: val
      }
      fetchTableColumnList(data).then(response => {
        this.tableColumnList = response.list
        this.ruleForm.excelConfigExcludeColumn = []
      })
    },

    // 根据表名获取字段
    getTableColumnLists(val, datas) {
      console.log('tableName: ' + val)
      const data = {
        tableName: val
      }
      fetchTableColumnList(data).then(response => {
        this.tableColumnList = response.list
        // 移除
        const list = [] // sex, address
        for (const obj in datas) { // sex, address, remark
          for (const obj2 in this.tableColumnList) {
            if (datas[obj] === this.tableColumnList[obj2].name) {
              list.push(datas[obj])
            }
          }
        }
        console.log('list: ' + list)
        const delNode = []
        // 获取this.ruleform.excel...要删除的值
        for (const obj in this.columnList) { // sex, address, remark
          let num = 0
          for (const obj2 in list) { // sex, address
            if (list[obj2] === this.columnList[obj]) {
              num = 1
              break
            }
          }
          if (num <= 0) {
            delNode.push(this.columnList[obj])
          }
        }
        console.log('delNode: ' + delNode)
        // 获取this.excel...要删除的值
        const delExclude = []
        for (const obj in list) { // sex, address
          let num = 0
          for (const obj2 in this.columnList) { // sex
            if (list[obj] === this.columnList[obj2]) {
              num = 1
              break
            }
          }
          if (num <= 0) {
            delExclude.push(list[obj])
          }
        }
        for (const obj in delExclude) {
          for (const obj2 in datas) {
            if (delExclude[obj] === datas[obj2]) {
              datas.splice(obj2, 1)
            }
          }
        }
        // 删除
        // if (this.type === 'edit') {
        const obj3 = this.ruleForm.excelConfigExcludeColumn
        for (const obj in delNode) { // remark
          for (let i = obj3.length - 1; i >= 0; i--) { // sex, address. remark
            if (delNode[obj] === obj3[i]) {
              obj3.splice(i, 1)
            }
          }
        }
        console.log(this.excelConfigExcludeColumn.length)
        // for (const obj in delNode) {
        //   for (const obj2 in obj3) {
        //     if (delNode[obj] === obj3[obj2]) {
        //       obj3.splice(obj2, 1)
        //       // demo.push(obj3[obj2])
        //     }
        //   }
        // }
        console.log('excelConfigExcludeColumn: ' + this.ruleForm.excelConfigExcludeColumn + ' - ' + this.excelConfigExcludeColumn)
        // }
      })
    },

    // 添加新标签
    handleInputConfirm(val) {
      if (val) {
        this.dynamicTags.push(val)
      }
    },

    // 关闭标签
    handleClose(tag) {
      this.dynamicTags.splice(this.dynamicTags.indexOf(tag), 1)
    },

    // 校验提交
    submitForm(formName) {
      if (this.selectNode === null || this.selectNode === undefined) {
        console.log('error submit!!')
        return false
      } else {
        let data = ''
        for (const obj in this.selectNode) {
          if (this.selectNode.length - 1 <= obj) {
            data += this.selectNode[obj]
          } else {
            data += this.selectNode[obj] + ','
          }
        }
        this.ruleForm.excelConfigTablename = data
      }

      this.$refs[formName].validate((valid) => {
        if (valid) {
          if (this.type === 'edit') {
            this.handleEdit()
          } else {
            this.handleSubmit()
          }
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },
    // 保存
    handleSubmit() {
      this.ruleForm.excelConfigTablename = this.selectNode.join(',')
      this.ruleForm.excelConfigExcludeColumn = this.excelConfigExcludeColumn.join(',')
      // this.ruleForm.excelConfigExcludeColumn = this.ruleForm.excelConfigExcludeColumn.join(',')
      saveData(this.ruleForm).then(response => {
        this.$emit('msg-from-child', 'edit')
      })
    },
    // 修改
    handleEdit() {
      this.ruleForm.excelConfigExcludeColumn = this.excelConfigExcludeColumn.join(',')
      // this.ruleForm.excelConfigExcludeColumn = this.ruleForm.excelConfigExcludeColumn.join(',')
      editData(this.ruleForm).then(response => {
        this.$emit('msg-from-child', this.type)
      })
    },
    // 取消
    cancel() {
      this.$emit('msg-from-child', 'cancel')
    },
    getInfo() {
      fetchEditInfo(this.id).then(response => {
        const data = response.excelConfig
        const nodes = data.excelConfigTablename.split(',')
        const nodeArray = []
        for (const obj in nodes) {
          for (const obj2 in this.data) {
            if (nodes[obj] === this.data[obj2].label) {
              nodeArray.push(obj2)
              this.selectNode.push(nodes[obj])
            }
          }
        }
        this.selectNodeArray = nodeArray
        this.watchNode = this.selectNode[0]
        // this.getTableColumnLists(this.selectNode[0])
        const list = data.excelConfigExcludeColumn.split(',')
        this.excelConfigExcludeColumn = JSON.parse(JSON.stringify(list))
        this.ruleForm = {
          excelConfigExcludeColumn: JSON.parse(JSON.stringify(list)),
          excelConfigExcludeId: data.excelConfigExcludeId,
          excelConfigId: data.excelConfigId,
          excelConfigKey: data.excelConfigKey
          // excelConfigTablename: data.excelConfigTablename
        }
        // this.$set(this.ruleForm, 'excelConfigExcludeColumn', list)
        // this.ruleForm.excelConfigExcludeColumn = this.ruleForm.excelConfigExcludeColumn.split(',')
        console.log(data.excelConfigExcludeColumn)
        // console.log(this.ruleForm.excelConfigExcludeColumn)
      })
    },
    // 获取表名列表
    getTableNameList() {
      fetchTableNameList().then(response => {
        this.tableNameList = response.list
        for (const obj in response.list) {
          const datas = {
            id: obj,
            label: response.list[obj]
          }
          this.data.push(datas)
        }
        if (this.type === 'edit') {
          this.getInfo()
        }
      })
    },
    // 选择表名之后
    selectTableNameChange(val) {
      this.handleInputConfirm(val)
      this.getTableColumnList(val)
    }
  }
}
</script>

<style>
  .test{
    width: 400px;
    height: 200px;
    overflow: auto;
    /* float: left; */
    margin: 5px;
    border: none;
  }
  /*滚动条整体样式*/
  /*高宽分别对应横竖滚动条的尺寸*/
  .test-1::-webkit-scrollbar {
    width: 10px;
    height: 1px;
    }
    /*滚动条里面小方块*/
  .test-1::-webkit-scrollbar-thumb {
    border-radius: 10px;
    -webkit-box-shadow: inset 0 0 5px rgba(0,0,0,0.2);
    background: #535353;
  }
  /*滚动条里面轨道*/
  .test-1::-webkit-scrollbar-track {
    -webkit-box-shadow: inset 0 0 5px rgba(0,0,0,0.2);
    border-radius: 10px;
    background: #EDEDED;
  }
  .scrollbar {
    width: 30px;
    height: 300px;
    margin: 5px;
  }
  .el-tag + .el-tag {
    margin-left: 10px;
    margin-bottom: 20px;
  }
  .button-new-tag {
    margin-left: 10px;
    height: 32px;
    line-height: 30px;
    padding-top: 0;
    padding-bottom: 0;
  }
  .input-new-tag {
    width: 90px;
    margin-left: 10px;
    vertical-align: bottom;
  }
</style>
