import request from '@/utils/request'

export function fetchList(query) {
  return request({
    url: '/excel/excelconfigByKeyOrTablename',
    method: 'get',
    params: query
  })
}

export function fetchExportExcelFile(data) {
  return request({
    url: '/excelfile/toExportExcel',
    method: 'get',
    params: data
  })
}

export function fetchExcelConfigSelect() {
  return request({
    url: '/excel/excelconfig/info',
    method: 'get'
  })
}

export function saveData(data) {
  return request({
    url: '/excel/excelconfig',
    method: 'post',
    data
  })
}

export function fetchEditInfo(id) {
  const url = '/excel/excelconfig/' + id
  return request({
    url,
    method: 'get'
  })
}

export function editData(data) {
  return request({
    url: '/excel/excelconfig',
    method: 'put',
    data
  })
}

export function deleteData(data) {
  return request({
    url: '/excel/excelconfig',
    method: 'delete',
    data
  })
}

export function fetchTableNameList(query) {
  return request({
    url: '/table/tableNameList',
    method: 'get',
    params: query
  })
}

export function fetchTableColumnList(query) {
  return request({
    url: '/table/tableColumnList',
    method: 'get',
    params: query
  })
}

export function uploadFile(data) {
  return request({
    url: '/excelfile',
    method: 'post',
    data
  })
}
