import request from '@/utils/request'

export function fetchList(query: any) {
  return request({
    url: '/excel/excelconfigByKeyOrTablename',
    method: 'get',
    params: query
  })
}

export function fetchExportExcelFile(data: any) {
  return request({
    url: '/excelfile/toExportExcel',
    method: 'get',
    params: data
  })
}

export function fetchExcelConfigSelect() {
  return request({
    url: '/excel/excelconfig/info',
    method: 'get'
  })
}

export function saveData(data: any) {
  return request({
    url: '/excel/excelconfig',
    method: 'post',
    data
  })
}

export function fetchEditInfo(id: any) {
  const url = '/excel/excelconfig/' + id
  return request({
    url,
    method: 'get'
  })
}

export function editData(data: any) {
  return request({
    url: '/excel/excelconfig',
    method: 'put',
    data
  })
}

export function deleteData(data: any) {
  return request({
    url: '/excel/excelconfig',
    method: 'delete',
    data
  })
}

export function fetchTableNameList(query: any) {
  return request({
    url: '/table/tableNameList',
    method: 'get',
    params: query
  })
}

export function fetchTableColumnList(query: any) {
  return request({
    url: '/table/tableColumnList',
    method: 'get',
    params: query
  })
}

export function uploadFile(data: any) {
  return request({
    url: '/excelfile',
    method: 'post',
    data
  })
}
